input[type="search"] {
  -webkit-appearance: textfield; }

input[type="number"] {
  -moz-appearance: textfield; }

input[type="number"]:hover {
  -moz-appearance: number-input; }

article, nav li {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }
